.form {
  padding-top: 165px;
  padding-bottom: 250px;
  background: linear-gradient(319deg, $color-deep-sky-blue 66.3%, $color-cerulean 66%);
}

.form__box {
  position: relative;
  max-width: 585px;
  margin-left: auto;
  padding-left: 5px;
  color: $color-default-white;
}

.form h2 {
  font-family: $ff-roboto-condensed;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin: 0;
  margin-bottom: 9px;
  margin-left: -15px;
}

.form p {
  margin: 0;
  margin-bottom: -3px;
  margin-left: -10px;
}

.form__container {
  margin-left: -13px;
  max-width: 585px;
}

.form__container--modal {
  width: 100%;
  margin-left: 0;
}

.form__inner,
.form__wrap {
  display: flex;
  gap: 30px;
  align-items: flex-end;
  margin-top: 37px;
}

.form__inner--reverse {
  flex-direction: row-reverse;
}

.form__field {
  display: inline-block;
  flex: 1 1;
  width: 100%;
}

.form__field input,
.form__comment textarea {
  padding: 15px 0 15px 15px;
  background: $color-pattens-blue;
  border: none;
  border-radius: 3px;
}

.form__field input {
  display: block;
  width: 100%;

  &::placeholder {
    color: $color-default-white;
  }

  &:not(:disabled):hover,
  &:not(:disabled):focus {
    outline: 1px solid $color-black-pearl;
  }

  &:active::placeholder {
    color: $color-default-white;
  }
}

.form__comment {
  margin-top: 20px;
}

.form__comment textarea {
  width: 100%;
  padding-top: 27px;
  padding-left: 14px;
  height: 155px;

  &::placeholder {
    color: $color-default-white;
  }

  &:not(:disabled):hover,
  &:not(:disabled):focus {
    outline: 1px solid $color-black-pearl;
  }

  &:active::placeholder {
    color: $color-default-white;
  }
}

.form__button {
  font-family: $ff-roboto-condensed;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  padding: 13px 70px;
  background: $color-black-pearl;
  border: none;
  border-radius: 3px;
  flex-shrink: 0;
  cursor: pointer;

  @include hover-focus {
    background: $color-alice-blue;
    outline: 2px solid $color-black-pearl;
    color: $color-black-pearl;
  }

  @include active {
    background: $color-alice-blue;
    outline: 2px solid $color-black-pearl;
    color: $color-black-pearl;
  }
}

.form__checkbox {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: -4px;
  cursor: pointer;
}

.form__checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.form__checkbox label {
  position: relative;
  display: inline-block;
  font-size: 14px;
  line-height: 16px;
  color: $color-default-white;
  opacity: 0.8;
  padding-left: 48px;
  cursor: pointer;

  @include hover-focus {
    color: $color-black-pearl;
  }
}

.form__checkbox span::before {
  position: absolute;
  content: "";
  left: 22px;
  top: 4px;
  width: 15px;
  height: 15px;
  border: 1px solid $color-default-white;
  border-radius: 2px;
}

.form__checkbox input[type="checkbox"]:checked + span::after {
  position: absolute;
  content: "";
  width: 7px;
  height: 7px;
  top: 8px;
  left: 25px;
  background-image: url("../img/svg/check-mark.svg");
}

.form__checkbox input:hover + span::before,
.form__checkbox input:focus + span::before {
  border: 1px solid $color-black-pearl;
}

.form--modal h2,
.form--modal p {
  margin-left: 0;
}

.form__inner--modal {
  flex-direction: column;
  margin-top: 35px;
  gap: 23px;
}

.form__comment--modal {
  margin-top: 22px;
}

.form__wrap--modal {
  flex-direction: column-reverse;
  align-items: stretch;
  gap: 18px;
  margin-top: 25px;
}

.form__checkbox--modal {
  justify-content: flex-end;
}

.form__checkbox--modal label {
  padding-left: 35px;
}

.form__checkbox--modal span::before {
  top: 1px;
  left: 8px;
}

.form__checkbox--modal input[type="checkbox"]:checked + span::after {
  top: 5px;
  left: 11px;
}

@media (max-width: ($vp-1440 - 1px)) {
  .form--modal h2,
  .form--modal p {
    margin-left: 0;
  }

  .form__box,
  .form__container {
    max-width: 500px;
  }

  .form__box::before {
    left: -90%;
    height: 223px;
    width: 400px;
  }

  .form__inner--reverse {
    gap: 0;
  }
}

@media (min-width: $vp-1024) {
  .form__box::before {
    position: absolute;
    content: "";
    width: 512px;
    height: 285px;
    top: 18%;
    left: -109%;

    @include graphic-retina("paper-airplane");
    background-size: 100%;
  }
}

@media (max-width: ($vp-1920 - 1px)) {
  .form {
    margin-right: 0;
    padding-bottom: 252px;
    background: linear-gradient(319deg, $color-deep-sky-blue 68%, $color-cerulean 68%);
  }

  .form__box::before {
    height: 284px;
  }

  .form h2 {
    margin-left: -11px;
    margin-bottom: 8px;
  }

  .form p {
    margin-bottom: 0;
    margin-left: -8px;
  }

  .form textarea {
    padding-left: 16px;
  }
}

@media (max-width: ($vp-1024 - 1px)) {
  .form {
    background: $color-deep-sky-blue;
    padding-top: 44px;
    padding-bottom: 125px;
  }

  .form__box {
    max-width: 100%;
    margin: 0;
    padding-left: 0;

    &::before {
      content: none;
    }
  }

  .form__container {
    max-width: 100%;
    margin-left: 0;
  }

  .form h2 {
    margin-left: 0;
    margin-bottom: 5px;
  }

  .form p {
    margin-left: 0;
  }

  .form__inner,
  .form__wrap {
    margin-top: 25px;
  }

  .form textarea {
    padding-top: 34px;
    padding-left: 14px;
  }

  .form__checkbox {
    margin-bottom: 0;
  }

  .form__button {
    margin-right: 34px;
    margin-top: 13px;
  }
}

@media (max-width: ($vp-768 - 1px)) {
  .form {
    padding-top: 47px;
    padding-bottom: 83px;
  }

  .form p {
    font-size: 14px;
    line-height: 134.4%;
    max-width: 200px;
    margin-bottom: 35px;
  }

  .form h2 {
    font-weight: 400;
    font-size: 30px;
    line-height: 140.62%;
    margin-bottom: 8px;
  }

  .form__inner,
  .form__wrap {
    flex-direction: column;
    gap: 20px;
    margin-top: 18px;
  }

  .form__field label {
    max-width: 100%;
  }

  .form__comment {
    margin: 0;
    margin-top: 23px;
  }

  .form textarea {
    padding-top: 24px;
    height: 180px;
  }

  .form__button {
    margin-right: 0;
    margin-bottom: 5px;
    width: 100%;
  }

  .form__checkbox {
    padding-left: 0;
  }

  .form__checkbox span::before {
    left: 4px;
    top: 5px;
  }

  .form__checkbox input[type="checkbox"]:checked + span::after {
    left: 7px;
    top: 9px;
  }
}
