@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PTMono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PTMono", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  transition: none;
}

.js-focus-visible:focus:not(.focus-visible) {
  outline: none;
}

@media (max-width: 767px) {
  .mobile-hidden {
    display: none;
  }
}

.wrapper {
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 350px;
}
@media (max-width: 1919px) {
  .container {
    padding: 0 110px;
  }
  .container--desktop-fullwidth {
    max-width: 1220px;
    padding: 0;
  }
}
@media (max-width: 1439px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
  .container--mobile-fullwidth {
    padding: 0;
  }
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 100%;
  min-height: 48px;
  margin-left: auto;
  padding: 14px;
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  outline: 1px solid #ff1553;
}
.custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:hover {
  outline: 1px solid #ff4475;
}
.custom-input.is-invalid input.focus-visible:focus,
.custom-input.is-invalid textarea.focus-visible:focus,
.custom-textarea.is-invalid input.focus-visible:focus,
.custom-textarea.is-invalid textarea.focus-visible:focus {
  outline: 1px solid #ff4475;
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  outline: 1px solid #096600;
}
.custom-input.is-valid input:hover,
.custom-textarea.is-valid input:hover {
  outline: 1px solid #3a8533;
}
.custom-input.is-valid input.focus-visible:focus,
.custom-textarea.is-valid input.focus-visible:focus {
  outline: 1px solid #3a8533;
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  line-height: 16px;
  font-size: 14px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff1553;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #096600;
}

.custom-input span {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.header {
  position: fixed;
  z-index: 100;
  width: 100%;
  background: linear-gradient(180deg, #041827 80%, rgba(255, 255, 255, 0));
  padding: 25px 0;
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 30px;
}

.header__logo {
  padding-top: 4px;
  flex-shrink: 0;
}

.header__list {
  display: flex;
  align-items: center;
  column-gap: 118px;
  padding-left: 0;
  padding-right: 6px;
  flex-wrap: wrap;
  list-style-type: none;
}

.header__link {
  display: flex;
  align-items: center;
  color: #f2f5f6;
}
.header__link:hover {
  color: #00b2ff;
}
.header__link:hover .header__icon {
  fill: #f2f5f6;
}
.header__link.focus-visible:focus {
  color: #00b2ff;
}
.header__link.focus-visible:focus .header__icon {
  fill: #f2f5f6;
}
.header__link:active {
  color: #c4c4c4;
}
.header__link:active .header__icon {
  fill: #c4c4c4;
}

.header__icon {
  fill: #00b2ff;
  margin-right: 15px;
}

.header__button {
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #f2f5f6;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  background-color: transparent;
  padding: 10px 15px;
  cursor: pointer;
  margin-right: 61px;
  min-width: 160px;
}
.header__button:hover {
  color: #041827;
  background-color: #f2f5f6;
}
.header__button.focus-visible:focus {
  color: #041827;
  background-color: #f2f5f6;
}
.header__button:active {
  color: #041827;
  border: 1px solid #00b2ff;
  background-color: #00b2ff;
}

@media (max-width: 1919px) {
  .header__list {
    column-gap: 123px;
  }
  .header__link {
    align-items: center;
  }
  .header__button {
    padding: 11px 16px;
  }
}
@media (max-width: 1439px) {
  .header__nav {
    max-width: 900px;
  }
  .header__button {
    margin-right: 0;
  }
  .header__list {
    gap: 30px;
  }
  .header__link {
    display: flex;
  }
  .header__icon {
    flex-shrink: 0;
  }
}
@media (max-width: 1023px) {
  .header {
    position: relative;
    padding: 5px 20px 25px 0;
  }
  .header__nav {
    justify-content: space-between;
    align-items: flex-end;
  }
  .header__list {
    gap: 20px;
    flex-wrap: wrap;
    padding-right: 0;
  }
  .header__link {
    padding: 10px;
  }
  .header__icon {
    margin: 0;
  }
  .header__text {
    display: none;
  }
  .header__button {
    display: none;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 10px 35px 20px 26px;
  }
  .header__nav {
    align-items: center;
  }
  .header__list {
    margin: 0;
  }
}
.footer {
  background-color: #041827;
  padding: 57px 0 0 0;
  margin-top: auto;
}

.footer__box {
  display: grid;
  grid-template-columns: 375px 500px 347px;
  justify-items: start;
}

.footer img {
  margin-top: -6px;
}

.footer__list {
  padding-left: 0;
  margin-bottom: 0;
}

.footer__list--nav {
  column-count: 2;
  column-gap: 48px;
}

.footer__item {
  break-inside: avoid-column;
}
.footer__item:last-child {
  margin-bottom: 92px;
}

.footer h2 {
  color: #ffffff;
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 21px;
}

.footer__link,
.footer p {
  display: inline-block;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 8px;
  margin-top: 0;
}

.footer__link {
  margin-bottom: 8px;
}
.footer__link:hover {
  color: #00b2ff;
}
.footer__link.focus-visible:focus {
  color: #00b2ff;
}
.footer__link:active {
  color: #ffffff;
}

@media (max-width: 1919px) {
  .footer {
    padding-top: 55px;
  }
  .footer img {
    margin-top: -7px;
  }
  .footer__contact {
    padding-left: 0;
  }
  .footer__link {
    margin-bottom: 7px;
  }
}
@media (max-width: 1439px) {
  .footer__box {
    grid-template-columns: 215px 315px 282px;
    justify-content: space-between;
  }
  .footer__list--nav {
    gap: 20px;
  }
}
@media (max-width: 1023px) {
  .footer {
    padding-top: 49px;
    padding-bottom: 65px;
  }
  .footer__box {
    display: grid;
    grid-template-areas: "social social" "nav contact";
    grid-template-columns: 1fr 1fr;
    row-gap: 30px;
    column-gap: 30px;
    justify-content: space-between;
  }
  .footer__social {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-area: social;
  }
  .footer__nav {
    grid-area: nav;
    padding-left: 0;
  }
  .footer__list--nav {
    column-count: 1;
  }
  .footer__contact {
    grid-area: contact;
  }
  .footer__list--column {
    display: flex;
    flex-direction: column;
  }
  .footer__link {
    margin-bottom: 7px;
  }
  .footer__item {
    max-width: 320px;
  }
  .footer__item:last-child {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .footer {
    padding-top: 44px;
  }
  .footer__box {
    display: flex;
    flex-direction: column;
    gap: 0;
  }
  .footer__social {
    flex-direction: column;
  }
  .footer__nav,
.footer__contact {
    position: relative;
    border-top: 1px solid rgba(248, 253, 255, 0.7);
  }
  .footer__nav {
    padding-top: 3px;
  }
  .footer__contact {
    margin-left: 0;
    margin-top: 0;
    padding-left: 0;
    padding-top: 2px;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
  .footer h2 {
    margin: 0;
    padding: 20px 0;
  }
  .footer p,
.footer__link {
    font-size: 14px;
    line-height: 135%;
    min-width: 280px;
  }
  .footer p {
    margin-bottom: 15px;
  }
  .footer__list {
    margin: 0;
    padding: 0 25px;
    padding-bottom: 10px;
  }
}
.social__list {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  padding-left: 0;
  margin-top: 35px;
  margin-left: 16px;
}

.social__icon {
  fill: #f8fdff;
}
.social__icon:hover {
  fill: #00b2ff;
}
.social__icon.focus-visible:focus {
  fill: #00b2ff;
}
.social__icon:active {
  fill: #ffffff;
}

@media (max-width: 1919px) {
  .social__list {
    gap: 25px;
    padding-left: 0;
  }
}
@media (max-width: 1023px) {
  .social__list {
    margin-top: 10px;
    padding-left: 297px;
  }
}
@media (max-width: 767px) {
  .social__list {
    margin-left: 0;
    margin-bottom: 22px;
    margin-top: 19px;
    padding-left: 0;
  }
}
.intro {
  position: relative;
  padding-top: 305px;
  padding-bottom: 280px;
  padding-left: 125px;
  background-color: #041827;
  background-position: center;
  width: 100%;
  height: auto;
  background-size: cover;
  color: #f2f5f6;
}
@supports (background-image: url("../img/background-image.webp")) {
  .intro {
    background-image: url("../img/background-image.webp");
  }
}
@supports not (background-image: url("../img/background-image.webp")) {
  .intro {
    background-image: url("../img/background-image.jpg");
  }
}
@media (min-resolution: 2dppx) {
  @supports (background-image: url("../img/background-image@2x.webp")) {
    .intro {
      background-image: url("../img/background-image@2x.webp");
    }
  }
  @supports not (background-image: url("../img/background-image@2x.webp")) {
    .intro {
      background-image: url("../img/background-image@2x.jpg");
    }
  }
}

.intro h2 {
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 32px;
}

.intro p {
  font-size: 22px;
  line-height: 155%;
  margin: 0;
  margin-bottom: 46px;
  max-width: 685px;
}

.intro__button {
  display: inline-block;
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 18px;
