.form {
  padding-top: 165px;
  padding-bottom: 250px;
  background: linear-gradient(319deg, #00b2ff 66.3%, #026593 66%);
}

.form__box {
  position: relative;
  max-width: 585px;
  margin-left: auto;
  padding-left: 5px;
  color: #ffffff;
}

.form h2 {
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin: 0;
  margin-bottom: 9px;
  margin-left: -15px;
}

