  margin: 0;
  margin-bottom: -3px;
  margin-left: -10px;
}

.form__container {
  margin-left: -13px;
  max-width: 585px;
}

.form__container--modal {
  width: 100%;
  margin-left: 0;
}

.form__inner,
.form__wrap {
  display: flex;
  gap: 30px;
  align-items: flex-end;
  margin-top: 37px;
}

.form__inner--reverse {
  flex-direction: row-reverse;
}

.form__field {
  display: inline-block;
  flex: 1 1;
  width: 100%;
}

.form__field input,
.form__comment textarea {
  padding: 15px 0 15px 15px;
  background: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
}

.form__field input {
  display: block;
  width: 100%;
}
.form__field input::placeholder {
  color: #ffffff;
}
.form__field input:not(:disabled):hover, .form__field input:not(:disabled):focus {
  outline: 1px solid #041827;
}
.form__field input:active::placeholder {
  color: #ffffff;
}

.form__comment {
  margin-top: 20px;
}

.form__comment textarea {
  width: 100%;
  padding-top: 27px;
  padding-left: 14px;
  height: 155px;
}
.form__comment textarea::placeholder {
  color: #ffffff;
}
.form__comment textarea:not(:disabled):hover, .form__comment textarea:not(:disabled):focus {
  outline: 1px solid #041827;
}
.form__comment textarea:active::placeholder {
  color: #ffffff;
}

.form__button {
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  padding: 13px 70px;
  background: #041827;
  border: none;
  border-radius: 3px;
  flex-shrink: 0;
  cursor: pointer;
}
.form__button:hover {
  background: #f2f5f6;
  outline: 2px solid #041827;
  color: #041827;
}
.form__button.focus-visible:focus {
  background: #f2f5f6;
  outline: 2px solid #041827;
  color: #041827;
}
.form__button:active {
  background: #f2f5f6;
  outline: 2px solid #041827;
  color: #041827;
}

.form__checkbox {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: -4px;
  cursor: pointer;
}

.form__checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.form__checkbox label {
  position: relative;
  display: inline-block;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  padding-left: 48px;
  cursor: pointer;
}
.form__checkbox label:hover {
  color: #041827;
}
.form__checkbox label.focus-visible:focus {
  color: #041827;
}

.form__checkbox span::before {
  position: absolute;
  content: "";
