  padding-bottom: 120px;
  margin: 0 auto;
  max-width: 1920px;
}

.data-accordion__box {
  position: relative;
  margin-left: 350px;
  padding-left: 249px;
}
.data-accordion__box::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 146px;
  height: 319px;
  background-repeat: no-repeat;
  background-size: 149px 319px;
}
